.labourcard {
    background-color: yellow;
    text-align: center;
    display: inline-block;
    margin: 10px;
    padding: 5px;
    border-radius: 10px;
    /* text-align: center; */
}

.image img {
    width: 200px;
    height: 200px;
    margin: 10px;
    background-color: yellowgreen;
    border-radius: 10px;
    /* background-color orangered; */
}

.labourcard p {
    font-family: 'Times New Roman', Times, serif;
    margin: 0px;
}

input[type="button"] {
    padding: 5px;
    border-radius: 5px;
}