div.step2 {
  display: inline-block;
  border: 2px solid black;
  height: 500px;
  width: 400px;
  margin: auto;
  background-color: #69f0ae;
  box-shadow: 2px 2px lightblue;
  /* margin-top: 50px; */
}
div.step2:hover {
  box-shadow: 1px 1px 2px 8px lawngreen;
  padding: 20px 20px;
  border-radius: 10px;
}
.parentbox {
  display: flex;
  justify-content: center;
  margin-top: 50px;
}
h1 {
  text-align: center;
}
h2 {
  text-align: center;
  text-decoration: underline;
}

.nextpage2 {
  background-color: #ff5983;
  width: 100px;
  padding: 10px;
  color: white;
  margin-left: 65%;
  border-radius: 10px;
  margin-top: 20px;
  display: block;
}
.nextpage2:hover {
  box-shadow: 1px 1px 2px 5px white;
  padding: 15px 15px;
  cursor: pointer;
}
