header {
  color: rgb(29, 29, 151);
  text-align: center;
  font-size: 50px;
  font-family: SFProDisplay-Bold, Helvetica, Arial, sans-serif;
}

.top {
  text-align: center;
  padding: 20px 20px;
  font-family: SFProDisplay-Bold, Helvetica, Arial, sans-serif;
  border: 1px ridge black;
  width: 450px;
  height: 500px;
  margin-left: 420px;
  border-radius: 10px;
  background-color: #ffffff;
}

.head {
  font-size: 25px;
}

.tablesignin {
  width: 440px;
  height: 500px;
  margin-top: 30px;
}

.tablesignin input[type="text"],
.tablesignin input[type="mobile"],
.tablesignin input[type="date"],
.tablesignin input[type="password"] {
  width: 410px;
  margin-top: 10px;
  height: 25px;
  font-size: 15px;
  margin-bottom: 10px;
}

.tablesignin input[type="radio"] {
  margin-top: 10px;
}

.tablesignin input[type="button"] {
  background-color: #00867d;
  color: aliceblue;
  margin-top: 30px;
  padding: 10px 15px;
  border-radius: 10px;
}

.tablesignin input[type="button"]:hover {
  background-color: green;
  color: aliceblue;
  margin-top: 30px;
  padding: 10px 30px;
  border-radius: 10px;
}
