.mark {
  height: 500px;
  border: 2px solid black;
  width: 400px;
  text-align: center;
  /* display: flex; */
  margin: auto;
  background-color: #69f0ae;
  box-shadow: 2px 2px lightblue;
  margin-top: 50px;
}
.mark:hover {
  /* box-shadow: 2px 2px lightblue; */
  box-shadow: 1px 1px 2px 8px lawngreen;
  padding: 20px 20px;
  border-radius: 10px;
}
.mark label {
  text-align: left;
  text-decoration: none;
}

.mark input[type="submit"] {
  background-color: #ff5983;
  width: 100px;
  padding: 10px;
  color: white;
  margin: auto;
  margin-top: 100px;
  border-radius: 10px;
  margin-top: 40px;
  display: block;
}
.mark input[type="submit"]:hover {
  box-shadow: 1px 1px 2px 5px white;
  padding: 15px 15px;
  cursor: pointer;
  width: 200px;
}

form {
  text-align: center;
}
.markform label {
  margin-top: 50px;
}

.nav nav {
  color: white;
  display: inline-block;
  background-color: darkcyan;
  border: 2px solid black;
  width: 100px;
  margin-top: 50px;
  cursor: pointer;
  height: 20px;
}
