.companyname {
    background-color: #00867d;
    color: white;
    padding: 25px 25px;
    text-align: center;
    font-family: Georgia, 'Times New Roman', Times, serif;
    font-size: 50px;
    font-stretch: wider;
    margin: 0px 0px
}

.fields input[type='text'] {
    width: 200px;
    margin-top: 10px;
    padding: 13px 13px;
    margin-left: 40%;
    letter-spacing: 1px;
    border-radius: 5px;
    text-align: center;
}


.fields input[type='password'] {
    width: 200px;
    padding: 13px 13px;
    margin-top: 5px;
    margin-left: 40%;
    letter-spacing: 1px;
    border-radius: 5px;
    text-align: center;
}

button {
    width: 228px;
    text-align: center;
    padding: 13px 13px;
    margin-top: 5px;
    margin-left: 40%;
    letter-spacing: 1px;
    border-radius: 5px;

}

.login {
    text-align: center;
    margin-right: 20px;
    color: black;
}