.table,
td,
th {
    border: 2px solid black;
    background-color: #4db6ac;
    border-collapse: collapse;
}
.parent{
    margin-top: 200px;
}

th {
    width: 100%;
}

tr {
    height: 50px;
}

tr:hover {
    background-color: #82e9de;
}

.icon {
    background-color: #82e9de;
    width: 220px;
    display: inline-block;
    height: 320px;
    border-radius: 10px;
    margin: 0px 10px;
    position: relative;
    text-align: center;
    margin-left: 150px;
    /* margin-top: 100px; */
}

img {
    width: 200px;
    background-color: #00867d;
    /* margin-left: 10px; */
    margin: 10px;
    /* border-color: solid dark yellowgreen 2px; */
    border-radius: 5px;
}



.viewbutton {
    /* margin-left: 70px; */
    padding: 5px;
    background-color: #4db6ac;
    border-radius: 2.5px;
}

td {
    width: 70%;
}

.table {
    display: inline-block;
    margin-left: 100px;
    height: auto;
    position: relative;
    top: -320px;
    left: 80px;
    width: 40%;
}

th {
    width: 100px;
    text-align: left;
}

span {
    /* margin-left: 70px; */
    font-size: larger;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    text-align: center;
}